import { fetchEventSource } from "@microsoft/fetch-event-source";
import { message } from "antd";

export enum RoleType {
  user = 'user',
  model = 'model',
}

export interface IMsg {
  Role: RoleType;
  Parts: string[];
}

interface IParams_Ask {
  History: IMsg[];
  Ask: string;
  onMsg: (res: IMsg) => void;
  onMsgDone?: () => void;
}


export const ask = async (params: IParams_Ask) => {
  console.log('~~~~ask')
  const { History, Ask, onMsg, onMsgDone } = params;

  const url = 'https://cl27qk.i996.me/chat';
  await fetchEventSource(url, {
    method: 'POST',
    headers: {
      'Content-Type': 'text/event-stream'
    },
    body: JSON.stringify({ History, Ask }),
    onmessage: (event) => {
      try {
        onMsg(JSON.parse(event.data));
      } catch(err) {

      }
    },
    onclose: () => {
      onMsgDone?.();
    },
    onerror: (err) => {
      console.error('~~~~~~on msg error', err)
      message.error({
        content: 'fetchEventSource error',
        key: 'fetchEventSource_error',
      });
      onMsgDone?.();
    },
    openWhenHidden: true,
  })
};