import React, { useRef, useState } from 'react';
import './index.scss';

import { Input } from 'antd';
import { IMsg, ask, RoleType } from '../../service/ask.ts';
import { marked } from 'marked';

const mockMsgList = [
  {
    Role: RoleType.user,
    Parts: [
      'nba'
    ]
  },
  {
    Role: RoleType.model,
    Parts: [
      ''
    ]
  },
]
export const ChatWindow = () => {
  const [msgList, setMsgList] = useState<IMsg[]>([]);
  const [input, setInput] = useState('');
  const [loading, setLoading] = useState(false);
  const currentMsgRef = useRef<IMsg | undefined>(undefined);
  const msgListRef = useRef<HTMLDivElement | undefined>(undefined);
  const compositionRef = useRef(false);

  const renderMsgList = () => {
    return (
      <div className='msg-list-wrap' ref={msgListRef}>
        {msgList.map((msg, i) => (
          <div className={`msg-item msg-item-${msg.Role}`} key={i}>
            <div
              className='msg-item-content'
              dangerouslySetInnerHTML={{
                __html: msg.Parts.map(p => marked.parse(p.replace('\n, ', '')))
              }}
            />
          </div>
        ))}
      </div>
    );
  };

  const renderHeader = () => {
    return (
      <h3>gpt</h3>
    );
  };

  const handleInputChange = (e) => {
    setInput(e.target.value.trim());
  };

  const handleAsk = () => {
    if (loading) {
      return;
    }
    setLoading(true);
    setMsgList((prev) => prev.concat({
      Role: RoleType.user,
      Parts: [input],
    }));
    setInput('');
    setTimeout(() => {
      if (msgListRef.current) {
        msgListRef.current.scrollTop = msgListRef.current.scrollHeight;
      }
    }, 50);
    ask({
      History: msgList,
      Ask: input,
      onMsg: (res: IMsg) => {
        const currentMsg = currentMsgRef.current;
        const newCurrentMsg: IMsg = {
          Role: RoleType.model,
          Parts: [
            ...(currentMsg?.Parts || []),
            ...res.Parts,
          ]
        };
        currentMsgRef.current = newCurrentMsg;
        setMsgList((prev) => {
          if (currentMsg) {
            return prev.slice(0, prev.length - 1).concat(newCurrentMsg)
          }
          return prev.concat(newCurrentMsg);
        });
        setTimeout(() => {
          if (msgListRef.current) {
            msgListRef.current.scrollTop = msgListRef.current.scrollHeight;
          }
        }, 50);
      },
      onMsgDone: () => {
        const currentMsg = currentMsgRef.current;
        if (currentMsg) {
          setMsgList((prev) => prev.slice(0, prev.length - 1).concat(currentMsg));
        }
        currentMsgRef.current = undefined;
        setLoading(false);
      }
    });
  };

  const handleKeyUp = (e) => {
    if (e.key === 'Enter' && !compositionRef.current) {
      handleAsk();
    }
  };

  const renderInput = () => {
    return (
      <Input
        value={input}
        onChange={handleInputChange}
        onKeyUp={handleKeyUp}
        placeholder={''}
        disabled={loading}
        onCompositionStart={() => { compositionRef.current = true; }}
        onCompositionEnd={() => { 
          setTimeout(() => {
            compositionRef.current = false;
          }, 100);
         }}
      />
    );
  };
  return (
    <div className='chat-window'>
      {renderHeader()}
      {renderMsgList()}
      {renderInput()}
    </div>
  );
};
